<template>
  <div class="ListofSingers">
    <router-link
      class="fanhui iconfont icon-back_icon"
      tag="div"
      to="/"
    ></router-link>
    <div class="ListofSingerson">
      <div class="ListofSingerimg">
        <img :src="`${img1v1Url}`" alt="" />
        <p>{{ pername }}</p>
      </div>
      <div class="ListofSingh3">
        <h3>歌手简介</h3>
        <span>{{ $route.query.playCount }}</span>
        <p class="xiangq" @click="ifshow = !ifshow">
          <span>详情&nbsp;</span
          ><span
            class="iconfont"
            :class="{ 'icon-xiala': ifshow, 'icon-shangla1': !ifshow }"
          ></span>
        </p>
        <p class="xiangqson" v-show="ifshow">{{ briefDesc }}</p>
      </div>
      <!-- {{$route.query.singdetailed }} -->
    </div>
    <div class="newest">
      <h2>
        总共10首歌曲
        <span
          class="iconfont icon-bofang"
          @click="$emit('play', hotSongs)"
        ></span>
      </h2>
      <newest-music
        v-for="hotSong in hotSongs"
        :key="hotSong.id"
        :item="hotSong"
        @play="$emit('play', $event)"
      ></newest-music>
    </div>
    <div class="musicbottom"></div>
  </div>
</template>
<script>
import NewestMusic from "@/components/NewestMusic";
export default {
  data() {
    return {
      briefDesc: "",
      hotSongs: "",
      ifshow: false,
      pername:'',
      img1v1Url:''
    };
  },
  components: {
    NewestMusic,
  },
  beforeRouteEnter(to, from, next) {
    // ...
    next((vm) => {
      vm.axios
        .get("/artist/desc?id=" + vm.$route.query.singdetailed)
        .then((d) => {
          vm.briefDesc = d.data.briefDesc;
          //d.data.list[0].tracks[0].first
          console.log(d);
        });
    });
    next((vm) => {
      vm.axios.get("/artists?id=" + vm.$route.query.singdetailed).then((d) => {
        // vm.hotSongs = d.data.hotSongs;
        vm.pername = d.data.artist.name
        vm.img1v1Url = d.data.artist.img1v1Url
        console.log(d);
        //d.data.list[0].tracks[0].first
        vm.allList = d.data.hotSongs.map((data) => {
          data.song = {
            artists: data.ar,
            album: data.al,
            alias: [],
          };
          data.picUrl = data.al.picUrl;
          return data;
        });
        vm.hotSongs = vm.allList.slice(0, 10);
        console.log(vm.hotSongs);
      });
    });
  },
};
</script>
<style lang="less" scoped>
.ListofSingers {
  .fanhui {
    position: absolute;
    top: 10px;
    left: 10px;
    color: white;
    font-weight: 600;
    font-size: 20px;
    z-index: 9999;
  }
  .ListofSingerson {
    .ListofSingerimg {
      width: 100%;
      height: 74vw;
      overflow: hidden;
      position: relative;
      p {
        width: 80%;
        padding: 3vw;
        position: absolute;
        text-align: center;
        border-radius: 8vw;
        color: #aaa;
        font-size: 6vw;
        background-color: #666666aa;
        top: 58vw;
        left: 10vw;
      }
    }
    .ListofSingh3 {
      h3 {
        color: #888;
        text-align: center;
        font-weight: 600;
        margin: 10px;
      }
      p {
        font-size: 14px;
        text-align: justify;
        margin: 10px 20px;
        span {
          color: #666;
          &:first-child {
            font-size: 5vw;
          }
        }
      }
      .xiangqson {
        text-indent: 2em;
      }
      .icon-xiala {
        font-size: 4vw;
      }
      .icon-shangla1 {
        font-size: 5vw;
      }
    }
  }
  .newest {
    h2 {
      margin: 2vw 0 4vw 5vw;
      display: flex;
      align-items: center;
      .icon-bofang{
        font-size:6vw;
      }
    }
  }
  .musicbottom {
    width: 100%;
    height: 12vw;
  }
}
</style>